<h1 id="android-permissions" tabindex="-1"><a class="header-anchor" href="#android-permissions" aria-hidden="true">#</a> Android Permissions</h1>
<p>Android has many features we can use (camera, file storage, Bluetooth communication, Internet communication, etc.), but these features should not be exploited by app developers. Therefore Android has a permissions system; before an application use a feature that might be harmful to the user the application must first obtain permission from the user to use the feature. Let's take a look at the details!</p>
