<div class="custom-container tip"><p class="custom-container-title">Jetpack...</p>
<p>The Android Jetpack libraries have a helper method called in activities and fragments called <code>registerForActivityResult()</code>, which is an alternative and easier way to ask for permission.</p>
</div>
